import { Component, OnInit } from '@angular/core';

declare interface MenuInfo {
  path: string;
  title: string;
}
export const MENU: MenuInfo[] = [
  { path: '/camera',       title: 'Camera' },
  { path: '/notification', title: 'Notificações' },
  { path: '/login',        title: 'Login' },
  { path: '/redux',        title: 'Redux' },
  { path: '/graphql',      title: 'GraphQL' }
];

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'POC CapacitorJS';
  menuItems: any[] = [];

  constructor() { }

  ngOnInit() {
    this.menuItems = MENU.filter(menuItem => menuItem);
  }
}
